*{margin:0}h1,h2,h3{font-family:'Raleway'}body{color:#5d5b6b;font-family:sans-serif;margin:0;padding:0;font-family:sans-serif}#header{background-color:#ececec;height:100px;position:sticky;top:0;width:100%;display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;grid-template-rows:auto;grid-template-areas:"header header header nav nav"}#header h1{display:inline;grid-area:header;margin-left:100px;cursor:pointer}.button-wrap{grid-area:nav;margin-top:20px;justify-self:center}.citation-wrap{display:grid;width:90%;justify-self:center;margin:auto;padding-top:20px}.citation{padding:5px}.event circle{fill:white;stroke:gray}.section{width:95%;margin-left:100px;margin-top:20px}.sub-section{margin-top:20px}.sketch{width:90%;height:auto;display:block;margin:auto}.sketch-wrap{display:flex;flex-direction:column;justify-content:center}.timeline{width:100%}@keyframes stripe-slide{0%{background-position:0% 0}100%{background-position:100% 0}}.btn{overflow:visible;margin:0;padding:0;border:0;background:transparent;font:inherit;line-height:normal;cursor:pointer;-moz-user-select:text;display:inline-block;text-decoration:none;text-transform:uppercase;padding:16px 36px 22px;background-color:#fff;color:#666;border:2px solid #666;border-radius:6px;margin-bottom:16px;margin-left:5px;transition:all .5s ease}.btn:-moz-focus-inner{padding:0;border:0}.btn--stripe{overflow:hidden;position:relative}.btn--stripe:after{content:'';display:block;height:7px;width:100%;background-image:repeating-linear-gradient(45deg, #666, #666 1px, transparent 2px, transparent 5px);-webkit-backface-visibility:hidden;backface-visibility:hidden;border-top:1px solid #666;position:absolute;left:0;bottom:0;background-size:7px 7px}.btn--stripe:hover{background-color:#666;color:#fff;border-color:#000}.btn--stripe:hover:after{background-image:repeating-linear-gradient(45deg, #fff, #fff 1px, transparent 2px, transparent 5px);border-top:1px solid #000;animation:stripe-slide 12s infinite linear forwards}.btn--large{width:50%}.btn--radius{border-radius:36px}
